.card {
  border-radius: 10px !important;
  border: none !important;
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
  transition: 0.6s ease !important;
}

.p-4.card:hover {
  box-shadow: 0px 10px 30px 20px rgba(0, 0, 0, 0.08) !important;
  transform: scale(1.1) !important;
  cursor: pointer;
}
.card-title {
  font-size: 1.4rem;
  font-weight: 700;
}
.card-text {
  font-size: 14px;
  color: #777777;
}
.card-deck a {
  width: 33%;
}

@media (max-width: 768px) {
  .card-deck a {
    width: 100%;
  }
}
